
* {
    font-size: 13px;
}

.Info {
    width: 1000px;
    height: auto;
    margin-left: auto;
    margin-right: auto;
    padding: 20px 30px;
    box-sizing: border-box;
}

.myBanner{
    width: 100%;
    height: 200px;
    background-color: #1a1d20;
    margin-bottom: 10px;
}

.myBanner2 {
    width: 470px;
    height: 200px;
    margin: 0 auto;
    border-bottom: 1px solid black;
}

.myAccount {
    margin-top: 10px;
}

.myAccount > tr > td {
    width: 100px;
    height: 50px;
}
.myAccount > tr > td:nth-of-type(2) {
    width: 300px;
}
